import { RouterView } from "vue-router";
const Tarefa = () =>
    import ("@/views/tarefa/Tarefas.vue");
const NovaTarefa = () =>
    import ("@/views/tarefa/NovoTarefa.vue");

const TarefaViews = {
    path: "tarefa",
    name: "Tarefa",
    component: RouterView,
    children: [{
            path: "/tarefas",
            name: "Tarefas",
            component: Tarefa,
            meta: {
                requiresAuth: true
            }
        },

        {
            path: "/tarefa/novo",
            name: "Novo cadastro de tarefa",
            component: NovaTarefa,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "visualizar/:tarefaId/:visualizar",
            name: "Visualizar cadastro de tarefa",
            component: NovaTarefa,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "editar/:tarefaId",
            name: "Editar cadastro de tarefa",
            component: NovaTarefa,
            meta: {
                requiresAuth: true
            }
        },
    ],
};

export default TarefaViews;