import { RouterView } from "vue-router";
const Js = () =>
    import ("@/views/cursos/js/introducao.vue");
const Variavel = () =>
    import ("@/views/cursos/js/Variaveis.vue");
const TipoDados = () =>
    import ("@/views/cursos/js/TipoDados.vue");
const Projeto = () =>
    import ("@/views/cursos/js/Projeto.vue");


const JsViews = {
    path: "js",
    name: "js",
    component: RouterView,
    children: [{
            path: "/js/introducao",
            name: "Itroduçao ao js",
            component: Js,
            // meta: {
            //     requiresAuth: true
            // }
        },
        {
            path: "/js/variaveis",
            name: "variaveis js",
            component: Variavel,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/js/tipodados",
            name: "tipo de dados js",
            component: TipoDados,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/js/projeto",
            name: "projeto js",
            component: Projeto,
            meta: {
                requiresAuth: true
            }
        },
    ],
};

export default JsViews;