<script>
import firebase from "firebase";
import { useRouter } from "vue-router";
import Swal from "sweetalert2";
import {
  VueCollapsiblePanelGroup,
  VueCollapsiblePanel,
} from "@dafcoe/vue-collapsible-panel";
import "@dafcoe/vue-collapsible-panel/dist/vue-collapsible-panel.css";
export default {
  components: {
    VueCollapsiblePanelGroup,
    VueCollapsiblePanel /* Multiselect */,
  },
  setup() {
    let userName = "Dev";
    let resp = firebase.auth();
    if (resp) {
      userName = resp.currentUser.email;
    }

    let router = useRouter();
    const sair = () => {
      firebase
        .auth()
        .signOut()
        .then(() => {
          router.push("/login");
        })
        .catch((error) => {
          console.log("erro", error);
        });
    };

    const resetar = () => {
      Swal.fire({
        icon: "warning",
        title: `Atenção`,
        text: `Digite o email para redefinição de senha`,
        input: "email",
        confirmButtonText: `Enviar`,
      }).then((response) => {
        if (response.isConfirmed) {
          firebase
            .auth()
            .sendPasswordResetEmail(response.value)
            .then(() => {
              Swal.fire({
                icon: "success",
                title: `Sucesso`,
                text: `Um e-mail com o link para redefinição de senha foi enviado para: "${response.value}`,
                confirmButtonText: `Fechar`,
              });
            })
            .catch((error) => {
              console.log("erro", error);
            });
        }
      });
    };

    return { sair, resetar, userName };
  },
};
</script>
