<script>
import firebase from "firebase";
import { ref } from "vue";
import Swal from "sweetalert2";
// import { useRouter } from "vue-router";
export default {
  name: "Signup",
  data() {
    return {
      email: "",
      password: "",
      xhrRequest: false,
      errorMessage: "",
      successMessage: "",
    };
  },
  setup() {
    // let router = useRouter();
    let ViewModel = ref({
      email: "",
      password: "",
      passwordConfirmation: "",
    });
    const signup = () => {
      if (ViewModel.value.password != ViewModel.value.passwordConfirmation) {
        console.log("As senhas não conferem");
      } else if (ViewModel.value.password.length < 6) {
        console.log("A senhas deve ter no minimo 6 caracteres");
      } else {
        firebase
          .auth()
          .createUserWithEmailAndPassword(
            ViewModel.value.email,
            ViewModel.value.password
          )
          .then(
            () => {
              //   v.successMessage = "Register Successfully.";
              //   v.xhrRequest = false;
              // document.getElementById("sib-form").submit();
              Swal.fire({
                icon: "success",
                title: `Sucesso`,
                text: `O e-mail "${ViewModel.value.email}" foi cadastre-se com sucesso`,
                confirmButtonText: `Entrar`,
              }).then(() => {
                firebase
                    .auth()
                    .signInWithEmailAndPassword(
                      ViewModel.value.email,
                      ViewModel.value.password
                    )
                    .then(
                      resp => {
                        // router.push("/home");
                        console.log("resp",resp.user.refreshToken)
                         localStorage.setItem("AutenticacaoCanalCodigoFrontEnd", resp.user.refreshToken);
                         document.getElementById("sib-form").submit();
                      },
                      (error) => {
                        console.log("erro", error.message);
                      }
                    );
                // if (response.isConfirmed) {
                  
                // }
              });
              console.log("Register Successfully");
            },
            (error) => {
              //   let errorResponse = JSON.parse(error.message);
              //   v.errorMessage = errorResponse.error.message;
              //   v.xhrRequest = false;
              Swal.fire({
                icon: "warning",
                title: `Atenção`,
                text: `${error.message}`,
                confirmButtonText: `Fechar`,
              });
              console.log("erro", error.message);
              
            }
          );
      }
    };
    return {
      ViewModel,
      signup,
    };
  },
  methods: {
    signupRequest() {
      let v = this;
      v.xhrRequest = true;
      v.errorMessage = "";
      v.successMessage = "";

      firebase
        .auth()
        .createUserWithEmailAndPassword(v.email, v.password)
        .then(
          () => {
            v.successMessage = "Register Successfully.";
            v.xhrRequest = false;
          },
          (error) => {
            let errorResponse = JSON.parse(error.message);
            v.errorMessage = errorResponse.error.message;
            v.xhrRequest = false;
          }
        );
    },
  },
};
</script>
