import { createRouter, createWebHistory } from 'vue-router';
import firebase from "firebase";
import HomeViews from './home/index';
import AppContaine from "../components/AppContainer.vue";
import AboutViews from './about/index';
// import About from "../views/About.vue"
import TarefaViews from "./tarefas/index";
import UsuarioViews from "./usuarios/index";
import Login from "../views/authentication/Login.vue";
import LoginViews from "./login/index";
import Signup from "../views/authentication/Signup.vue";
import CursoSiteDo0Views from "./cursos/index";
import HtmlViews from "./cursos/html/index";
import CssViews from "./cursos/css/index";
import JsViews from "./cursos/js/index"
import VueViews from "./cursos/vue/index";


const routes = [{
        path: '/app',
        name: 'Main',
        component: AppContaine,
        children: [
            HomeViews,
            AboutViews,
            TarefaViews,
            UsuarioViews,
            LoginViews,
            CursoSiteDo0Views,
            HtmlViews,
            CssViews,
            JsViews,
            VueViews
        ]
    },
    {
        path: '/',
        name: '/',
        component: Login,


    },
    {
        path: '/login',
        name: 'Login',
        component: Login,


    },
    {
        path: '/signup',
        name: 'signup',
        component: Signup,


    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})


router.beforeEach(async(to, from, next) => {
    const authenticatedUser = await firebase.auth().currentUser;
    console.log("authenticatedUser", authenticatedUser)
    const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
        // console.log("requiresAuth", requiresAuth)





    if (requiresAuth && !authenticatedUser) {
        // alert("You are not authorized to access this area.");
        next("login");
    } else {
        next();
    }
});



export default router