import { RouterView } from "vue-router";
const Usuario = () =>
    import ("@/views/usuarios/Usuarios.vue");
const NovaUsuario = () =>
    import ("@/views/usuarios/NovoUsuarios.vue");

const UsuarioViews = {
    path: "usuario",
    name: "Usuario",
    component: RouterView,
    children: [{
            path: "/usuarios",
            name: "Usuario",
            component: Usuario,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/usuario/editar/:usuarioId",
            name: "Editar cadastro de usuario",
            component: NovaUsuario,
            meta: {
                requiresAuth: true
            }

        },
        {
            path: "/usuario/novo",
            name: "Novo cadastro de usuario",
            component: NovaUsuario,
            meta: {
                requiresAuth: true
            }

        },
        {
            path: "/usuario/visualizar/:usuarioId/:visualizar",
            name: "Visualizar cadastro de usuario",
            component: NovaUsuario,
            meta: {
                requiresAuth: true
            }

        }
    ],
};

export default UsuarioViews;